.container_comment {
  display: flex;
  flex-direction: column;
  padding: 0px 10px 0 10px;
  @include tablet {
    width: 768px;
  }
  @include desktop {
    width: 1200px;
  }
}

.comment {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background: #d1d1d1;
  @include tablet {
    width: 768px;
  }
  @include desktop {
    width: 1200px;
  }
}

.comment_elem {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.coment_number {
  @include flex(center, center, null, null);
  width: 30px;
  height: 30px;
  background: #a3a3a3;
}

.comment_on {
  @include flex(center, center, null, null);
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.88;
  text-align: center;
  letter-spacing: 0.06em;
  color: rgba(0, 0, 0, 0.5);
}

.comment_but {
  height: 20px;
  padding: 0 10px 0 10px;
}

.comment_block {
  display: flex;
  flex-direction: column;
  // padding: 0px 10px 0 10px;
  @include tablet {
    width: 768px;
  }
  @include desktop {
    width: 1200px;
  }
}

.comment_text {
  width: 100%;
  height: 100px;
  resize: none;
  outline: none;
  // height: 30vh;
  // padding: 1em;
  // font-size: 1.5em;
  // text-align: left;
  // border: 1px solid #000;

  // box-sizing: border-box;

  border-top: 0px;
  padding-left: 10px;
}

